import React, { useEffect, useRef } from "react";
import { refreshWidgetData } from "./services/api/refreshService";
import useToast from "./hooks/useToast";
import useDroppableArea from "./hooks/useDroppableArea";
import { MSG_REFRESH_ERROR, MSG_REFRESH_SUCCESS } from "./utils/toastMessages";
import store from "./store"; // ✅ Import Redux store directly

const WidgetLifecycle = () => {
  const { handleDrop } = useDroppableArea();
  const { showSuccessToast, showErrorToast } = useToast();

  const initialDraggedDataRef = useRef(null); // ✅ Store latest initialDraggedData here

  console.log("---[WidgetLifecycle]--- starts");

  useEffect(() => {
    console.log("[WidgetLifecycle] 📌 Checking Redux store for initialDraggedData...");

    const state = store.getState();
    const initialDraggedData = state.droppedObject.droppedObjectData.initialDraggedData;

    if (!initialDraggedData?.data?.items?.length) {
      console.warn("[WidgetLifecycle] 🚨 No initialDraggedData available yet.");
    } else {
      initialDraggedDataRef.current = initialDraggedData; // ✅ Store latest Redux value in ref
      console.log("[WidgetLifecycle] ✅ Stored initialDraggedData:", initialDraggedDataRef.current);
    }
  }, []); // ✅ Run once on mount to store the latest value

  const hasOnRefreshRegistered = useRef(false); // ✅ Prevent duplicate `onRefresh` registrations

  useEffect(() => {
    if (!window.widget) return;

    if (hasOnRefreshRegistered.current) {
      console.warn("[WidgetLifecycle] ⏳ onRefresh is already registered. Ignoring duplicate.");
      return;
    }

    console.log("[WidgetLifecycle] 📌 Registering `onRefresh` event...");
    hasOnRefreshRegistered.current = true; // ✅ Prevent duplicate registrations

    const onRefresh = async () => {
      console.log("[WidgetLifecycle] 🔄 `onRefresh` triggered!");

      const latestState = store.getState(); // ✅ Fetch latest Redux state dynamically
      const latestDraggedData = latestState.droppedObject.droppedObjectData.initialDraggedData;

      if (!latestDraggedData?.data?.items?.length) {
        console.error("[WidgetLifecycle] ⚠️ `initialDraggedData` is missing or invalid:", latestDraggedData);
        return; // ✅ Prevents execution if data is invalid
      }

      initialDraggedDataRef.current = latestDraggedData; // ✅ Update ref with latest data

      const objectId = latestDraggedData.data.items[0]?.objectId;
      const objectType = latestDraggedData.data.items[0]?.objectType;

      if (!objectId || !objectType) {
        console.error("[WidgetLifecycle] ❌ Missing `objectId` or `objectType`. Skipping refresh.");
        return;
      }

      try {
        console.log("[WidgetLifecycle] 🚀 Refreshing widget with latest data...");
        await refreshWidgetData(latestDraggedData.data.items, handleDrop);
        showSuccessToast(MSG_REFRESH_SUCCESS);
      } catch (error) {
        console.error("[WidgetLifecycle] ❌ Error during refresh:", error);
        showErrorToast(MSG_REFRESH_ERROR);
      }
    };

    window.widget.addEvent("onRefresh", onRefresh);
    console.log("[WidgetLifecycle] ✅ `onRefresh` event registered successfully");
  }, []); // ✅ Ensures this runs **only once**

  return null;
};

export default WidgetLifecycle;
